<script setup>
import {ref,onMounted,reactive} from "vue"
import { getAuth, signInWithEmailAndPassword,signOut, onAuthStateChanged } from "firebase/auth";

const email=ref('');
const password=ref('');
const errMsg=ref('');
const userData=reactive({user:"",userUid:""})
const register=()=>{
    const auth = getAuth();
    signInWithEmailAndPassword(auth, email.value, password.value)
        .then((data) => {
            // Signed up 
            console.log("Suecess");
            console.log(data)
            // ...
        })
        .catch((error) => {
            const errorCode = error.code;
            const errorMessage = error.message;
            console.log(error.code);
            switch(error.code){
                case "auth/invalid-email":
                    errMsg.value="無效的電子郵件";
                    break;
                case "auth/invalid-login-credentials":
                    errMsg.value="電子郵件尚未註冊或帳號密碼錯誤";
                    break;
                default:
                    errMsg.value="帳號密碼錯誤";
                    break;

            }
            // ..
        });
          
       
       
}
const sighOut=()=>{
    const auth = getAuth();
    signOut(auth).then((res) => {
        console.log(res);
        console.log("signOut")
  // Sign-out successful.
}).catch((error) => {
  // An error happened.
});
}
const signGoogle=()=>{

}
onMounted(()=>{
    const auth = getAuth();
    onAuthStateChanged(auth, (user) => {
            if (user) {
                // User is signed in, see docs for a list of available properties
                // https://firebase.google.com/docs/reference/js/auth.user
                userData.user=user.email;
                userData.userUid=user.uid
                // ...
            } else {
                // User is signed out
                // ...
            }
            });  
});

// firebase
//   .auth()
//   .createUserWithEmailAndPassword(email, password)
//   .then(result => {
//     console.log(result)
//   }).catch(function(error) {
//     console.log(error.message)
//   });
</script>

<template>
    <div>
        <h1>登入</h1>
        <div>
            
            <label for="">帳號</label>
            <input class="form-control" type="text" v-model="email" />
            <label for="">密碼</label>
            <input class="form-control" type="text" v-model="password" />
            <button class="btn btn-primary" @click="register">提交</button>
            <button class="btn btn-primary" @click="signGoogle">signin with google</button>
        </div>
        <p v-if="errMsg">{{ errMsg }}</p>
        <button class="btn btn-danger" @click="sighOut">登出</button>
    </div>
            您已成功登入。
            帳號:{{userData.user}}<br />
            UID:{{userData.userUid}}
    <div>

    </div>
</template>
